<template>
  <div>
    <h1 class="text-center">Editar libro {{ idBook }}</h1>

    <v-form v-model="valid" ref="form">
      <v-container>
        <v-row>
          <v-col>
            <v-switch
              v-model="allowEdit"
              label="Editar Libro"
              color="info"
              value="info"
              hide-details
            >
            </v-switch>
          </v-col>
          <v-col cols="12">
            <v-text-field
              v-model="form.title"
              :rules="requiredRules"
              :counter="60"
              label="Nombre del libro"
              required
              :disabled="!allowEdit"
            >
            </v-text-field>
          </v-col>

          <v-col cols="12">
            <v-text-field
              v-model="form.author"
              :rules="requiredRules"
              :counter="20"
              label="Nombre del autor"
              required
              :disabled="!allowEdit"
            >
            </v-text-field>
          </v-col>

          <v-col cols="12">
            <v-text-field
              v-model="form.image"
              :rules="requiredRules"
              label="Src"
              required
              :disabled="!allowEdit"
            ></v-text-field>
          </v-col>

          <v-col cols="12">
            <v-select
              :items="types"
              label="Tipo"
              v-model="form.type"
              :rules="requiredRules"
              :disabled="!allowEdit"
            ></v-select>
          </v-col>

          <v-col>
            <v-btn
              color="blue"
              :rules="requiredRules"
              :disabled="!allowEdit"
              @click="editBook"
              >Editar
            </v-btn>
            <v-btn color="yellow" @click="resetForm">Limpiar</v-btn>
            <v-btn color="red" dark @click="dialog=true">Eliminar</v-btn>
          </v-col>
        </v-row>
      </v-container>
    </v-form>
    <v-dialog v-model="dialog" width="500">
      <v-card>
        <v-card-title class="text-h5 grey lighten-2">
          Borrar libro?
        </v-card-title>

        <v-divider></v-divider>

        <v-card-actions>
          <v-btn color="red" text @click="deleteBook">Eliminar</v-btn>
          <v-spacer></v-spacer>
          <v-btn color="primary" text @click="dialog = false">Cerrar</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
import { mapState } from "vuex";
import { db } from "@/firebase/firebase.js";
import { deleteDoc, doc, getDoc, updateDoc } from "firebase/firestore";

export default {
  name: "book-comp",
  props: ["idBook"],
  data: function () {
    return {
      valid: true,
      dialog: false,
      form: {
        title: "",
        author: "",
        type: "",
        image: "",
      },
      requiredRules: [(v) => !!v || "Este campo es requerido"],
      allowEdit: false,
    };
  },
  computed: {
    ...mapState(["types"]),
  },
  methods: {
    resetForm() {
      this.$refs.form.reset();
    },

    async fetchBookById() {
      const docRef = doc(db, "books", this.idBook);
      let response = await getDoc(docRef);
      console.log(response.data());
      this.form = response.data();
    },
    async editBook() {
      try {
        let book = this.form;
        book.id = this.idBook;
        await updateDoc(doc(db, "books", book.id), book);
        this.$router.push("/");
      } catch (error) {
        console.log(error);
      }
    },
    async deleteBook(){
        try{
            await deleteDoc(doc(db,'books',this.idBook))
            this.$router.push('/')
        }
        catch(error){
            console.log(error)
        }
    }
  },
  // watch: {},
  // components: {},
  // mixins: [],
  // filters: {},
  // -- Lifecycle Methods
  created() {
    this.fetchBookById();
  },
  // -- End Lifecycle Methods
};
</script>

<style scoped>
form {
  display: grid;
  width: 500px;
  margin: 0 auto;
}
</style>
