<template>
  <v-app-bar app color="primary" dark>
    <div class="d-flex align-center">
      <v-img
      src="../assets/Libros.png"
      class="imagen"
      ></v-img>
    </div>

    <v-spacer></v-spacer>

    <v-btn @click="$router.push('/book')" text>
      <v-icon>mdi-plus-circle</v-icon>
      <span class="mr-2">Nuevo Libro</span>
    </v-btn>
    <v-btn color="info" @click="logout">Logout</v-btn>
  </v-app-bar>
</template>

<script>
import { getAuth, signOut } from "firebase/auth";

export default {
  name: "navbar-component",
  // props: {},
  data: function () {
    return {};
  },
  // computed: {},
  methods: {
    logout() {
      const auth = getAuth();
      signOut(auth)
        .then(() => {
          // Sign-out successful.
          //deslogueado del sistema
          this.$router.push('/login')
        })
        .catch((error) => {
          // An error happened.
          console.log(error)
        });
    },
  },
  // watch: {},
  // components: {},
  // mixins: [],
  // filters: {},
  // -- Lifecycle Methods
  // -- End Lifecycle Methods
};
</script>

<style scoped>
.imagen{
  width: 100px;
}
</style>
