<template>
  <div>
    <v-container>
      <v-row justify="center">
        <v-col md="5">
          <h2>Crear nuevo usuario</h2>
          <v-form>
            <v-text-field
              v-model="user"
              type="email"
              label="Correo"
            ></v-text-field>
            <v-text-field
              v-model="pass"
              type="password"
              label="Contraseña"
            ></v-text-field>
            <v-btn @click="create" block color="primary">Crear</v-btn>
          </v-form>
        </v-col>
      </v-row>
    </v-container>
  </div>
</template>

<script>
import { getAuth, createUserWithEmailAndPassword } from "firebase/auth";

export default {
  name: "createUser-view",
  // props: {},
  data: function () {
    return {};
  },
  // computed: {},
  methods: {
    create() {
      const auth = getAuth();
      createUserWithEmailAndPassword(auth, this.user, this.pass)
        .then((userCredential) => {
          // Signed in
          const user = userCredential.user;
          console.log(user)
          this.$router.push('/')
          // ...
        })
        .catch((error) => {
          //const errorCode = error.code;
          const errorMessage = error.message;
          alert(errorMessage)
          // ..
        });
    },
  },
  // watch: {},
  // components: {},
  // mixins: [],
  // filters: {},
  // -- Lifecycle Methods
  // -- End Lifecycle Methods
};
</script>

<style scoped></style>
